// ================
// used in new HTML
// ================
.feature-section {
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;

  @include media_min(lg){
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .scene {
    position: absolute;
    right: 0;
    top: 30%;
    display: none;

    @include media_min(lg){
      display: block;
      max-width: 1200px;
      height: 800px;
    }
  }

  .feature-items {
    padding-top: 0;
    padding-bottom: 0;

    @include media_min(lg){
      padding-top: 25px;
      padding-bottom: 25px;
    }

    @include media_min(xl){
      padding-top: 250px;
      padding-bottom: 250px;
    }

    .col-12 {

      &:nth-child(1) {

        .feature-item {
          animation-delay: 0.05s;
        }

      }

      &:nth-child(2) {

        .feature-item {
          animation-delay: 0.15s;
        }

      }

      &:nth-child(3) {

        .feature-item {
          animation-delay: 0.25s;
        }

      }

      &:nth-child(4) {

        .feature-item {
          animation-delay: 0.35s;
        }

      }
    }

    .feature-item {
      padding: 25px;
      text-align: center;
      height: 100%;
      background: #fff;
      color: #000;
      box-shadow: 0px 5px 15px rgba(0, 0, 0, .08);
      @include animateEaseIn(0.2s);
      overflow: hidden;
      position: relative;
      @include media(md){
        padding-bottom: 0;
        padding:15px;
      }
      @include media_min(lg){
        box-shadow: 0px 5px 15px rgba(0, 0, 0, .25);
        padding: 10px;
      }

      &:before {
        content: '';
        position: absolute;
        z-index: 0;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: 0;
        visibility: hidden;
        background:$orange;
        // background: -moz-linear-gradient(top,  #9b23ea 0%, #5f71bd 100%);
        // background: -webkit-linear-gradient(top,  #9b23ea 0%,#5f71bd 100%);
        // background: linear-gradient(to bottom,  #9b23ea 0%,#5f71bd 100%);
        // filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#9b23ea', endColorstr='#5f71bd',GradientType=0 );
        @include animateEaseIn(0.2s);
      }
      &:hover {
        &:before {
          opacity: 1;
          visibility: visible;
        }
      }

      .thumb, h4, p {
        position: relative;
        z-index: 1;
      }

      .thumb {
        display: block;
        width: 110px;
        height: 110px;
        margin: 25px auto;
        padding: 15px;
        position: relative;
        background: #fff;
        opacity: 1;
        @include border-radius(50%);
        @include media(md){
          margin-top: 0;
          margin-bottom: 20px;
          padding-top: 0;
        }
        img,
        svg {
          width: 100%;
          height: 100%;
          position: relative;
          z-index: 3;
          color: #9c2fe9;
          fill: currentColor;
          @include animateEaseIn(0.4s);
        }

        &:before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          z-index: 2;
          width: 100%;
          height: 100%;
          background: #fff;
          @include border-radius(50%);
        }

        &:after {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          z-index: 1;
          width: 100%;
          height: 100%;
          background: url(../img/svg/fround.svg) center no-repeat;
          transform: scale(1.4);
          @include animateEaseIn(0.4s);
        }
      }

      h4 {
        padding: 5px 0 5px;
        font-size: 24px;
        font-weight: 500;
        color: $black;

        @include media(lg){
        font-size: 18px;
        }
      }

      p {
        padding: 15px 0;
        font-size: 18px;
        font-weight: 300;
        color: #686868;

      @include media(lg){
        padding-top: 0px;
        font-size: 14px;
      }
      }

      &:hover {
        color: #fff;

        .thumb {
          background: transparent;

          &:before {
            opacity: 0;

          }

          &:after {
            left: 100%;
            top: -100%;
            transform: scale(6);
            opacity: .5;
          }

          svg {
            color: rgba(255,255,255,.7);
          }
        }

        h4 {
          color: #fff;
        }

        p {
          color: #fff;
        }
      }

    }
  }
}

