    font-size: 14px;
  }
}
// common style 
section,footer{
  h3.sub-title{
    font-size: 36px;
    line-height: 1.3;
    @include media(xl) {
      font-size: 28px;
      margin: 0 0 20px;
      padding: 0 0 30px;
    }   
    @include media(md) {
      font-size: 22px;
      margin: 0 0 10px;
      padding: 0 0 20px;
    }
    @include media(md) {
      font-size: 20px;
    
    }
  }
  h5{
    font-size: 22px;
    line-height: 1.3;
    font-weight:normal;
    @include media(md) {
      font-size: 16px;
    }
    @include media(xs) {
      font-size: 16px;
    
    }
  }
}
.title_heading,.h1{
  font-size: 66px;
  line-height: 1.2;
  color:$bannerheading;
}

.curved-section:before{
  background: url(../img/new-images/curve-bg.png);
  width: 100%;
  height: 470px;
  content: "";
  display: block;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  @include media(xl) {
    height: 300px;
  }
  @include media(lg) {
    height: 200px;
  }
  @include media(sm) {
    height:100px;
  }
  @include media(xs) {
    height:40px;
  }
}
section{
  padding-top:100px;
  @include media(xl) {    
    padding-top: 80px;
  }
  @include media(sm) {
    padding-top: 60px;
  }
  @include media(xs) {
    padding-top: 40px;
  }
}
// common style end


.section-pos{
  .pos-slick{
    padding-bottom:50px;
    position: relative;
    .slick-dots{
      bottom: 0;
      left: 0;
      position: absolute;
      right: 0;
      padding: 0;
      @include flexbox;
      @include justify-content(center);
      margin: 0;
      li{
        margin:0 8px;
        button{
          width: 14px;
          height: 14px;
          background: $black;
          border: none;         
          @include border-radius(100%);
          font-size: 0;
          cursor: pointer;
          padding: 0;
          outline: none;
        }
      &.slick-active{
        button{
          background: $button;
        }
      }
      }
    }
  }
  .pi-list{        
    @include justify-content(space-between);
    text-align: center;
    padding:0;
    @include align-items(flex-start);
    li{
      // width:80px;
      @include flexbox;
      @include justify-content(center);
      @include align-items(center);
      @include flex-direction(column);
      .pi-list-thumb{
        width:80px;
        height: 80px;
        border: 3px solid $green;
        box-shadow: 2px 0 9px #cecccc;        
        @include flexbox;
        @include justify-content(center);
        @include align-items(center);
        @include media(md) {
          width:65px;
          height: 65px;
        }
      }
      .pi-list-title{
        font-size: 16px;
        color: $text-secondary;
        line-height: 1.3;
        margin-top: 15px;
        @include media(md) {
          font-size: 14px;
        }
      }
      &+li{
        margin-left: 50px;
        @include media(xl) {
          margin-left: 15px;
        }
        @include media(md) {
          margin-left: 5px;
        }
      }
      @include media(md) {
        flex:1;
      }
    }
  }
}

// client say section

.clientsay-section{
  margin-top: 110px;
  @include media(xl) {
    margin-top: 80px;
  }
  @include media(sm) {
    margin-top: 0px;
  }
  position: relative;
  .client-slick{
    margin-top: 80px;
    padding-bottom: 30px;
    @include media(xl) {
      margin-top: 15px;
    }
    @include media(xs) {
      margin-top: 0px;
    }
    .client-thumb{
      height: 240px;
      width: 240px;
      @include border-radius(100%);
      box-shadow: 0px 0px 90px rgb(184 124 214 / 28%);
      @include flexbox;
      @include justify-content(center);
      @include align-items(center);
      margin: 0 auto;
      background-color: $white;
      @include media(sm) {
        box-shadow: 0px 0px 10px rgb(184 124 214 / 28%);
        margin-top: 10px;
      }
      @include media(xs) {
        height: 140px;
        width: 140px;
        padding: 15px;
        margin-top: 0;
      }
    }
    .client-slick-right{
      font-size: 18px;
      line-height: 30px;
      color: $text-primary;
      @include media(md) {
        font-size: 14px;
        line-height: 1.3;
      }
      &::before{
        background: url(../img/new-images/quotes-icon.png);
        content: "";
        display: block;
        height: 170px;
        width: 200px;
        background-size: cover;
        opacity: 0.1;
        
        @include media(md) {
          height: 70px;
          width: 90px;
        }
        @include media(sm) {
          height: 50px;
          width: 60px;
          left: 0;
          right: 0;
          margin: 40px auto 0;
        }
        @include media(xs) {
          margin: 10px auto 0;
        }
      }
      .client-slick-title{
        font-size: 24px;
        color: $text-secondary;
        font-weight: bold;
        margin: 15px 0;
        @include media(md) {
          font-size: 20px;
        }
        @include media(xs) {
          font-size: 14px;
        }
      }
      @include media(sm) {
        text-align: center;
      }
    }
    .slick-arrow{
      background: transparent;
      border: none;
      font-size: 0;
      outline: none;
      position: absolute;
      right: calc(50% - 90px);
      bottom: 0;
      @include media(sm) {
        right: calc(50% - 40px);
      }
      &.slick-prev{
        right: calc(50% - 50px);
        transform: rotate(-180deg);
        @include media(sm) {
          right: calc(50% + 10px);
        }
      }
      &::before{
        background: url(../img/new-images/right-arrow.png);
        width:23px;
        height: 15px;
        content: "";
        display: block;
      }
    }
    .slick-slide{
      min-height: 380px;
      @include media(md) {
        min-height: 340px;
      }
    }
    .slick-track{
      @include flexbox;
    }
  }
  &:before,&:after{
    background: url(../img/new-images/bg-dot.png);
    content: "";
    height: 170px;
    width: 170px;
    display: block;
    position: absolute;
    right: 70px;
    top:110px;
    background-size: 100% ,contain;
    z-index: -1;
    @include media(xxl) {
      height: 110px;
      width: 100px;
    }
    @include media(lg) {
      height: 70px;
      width: 60px;
      right:0;
    }
    @include media(xs) {
      display: none;
    }
  }
  &:after{
    left: 6%;
    right: auto;
    height: 130px;
    width: 130px;
    top: 40%;
    @include media(xxl) {
      height: 110px;
      width: 100px;
      // top: 20px;
    }
  }
}

