// feature section start
.feature-section{
  position: relative;
  margin-top: -130px;
  @include media(xs) {
    margin-top: 0px;
  }
  &:before{
    background-image: url(../img/new-images/curve-bg-light.png);
    height: 320px;
    @include media(lg) {
      height: 200px;
    }
    @include media(xs) {
      height: 70px;
    }
  }
  &:after{
    background: url(../img/new-images/bg-dot.png);
    display: block;
    position: absolute;
    content: "";
    top: -270px;
    left: auto;
    width: 200px;
    height: 340px;
    background-size: contain;
    right: -50px;
    z-index: -1;
    @include media(xxl) {
      height: 340px;
      top: -130px;
      width: 140px;
    }
    @include media(xs) {
      display: none;
    }
  }
  .section-feature-inner{
    background-image: -webkit-gradient(linear, left top, left bottom, from(#fff1e6), to(white));    
    background-image: -o-linear-gradient(#fff1e6, white);    
    background-image: linear-gradient(#fff1e6, white);
}

  .feature-slick{
    padding-bottom: 105px;
    margin-bottom: 75px;
    @include media(md) {
      margin-bottom: 45px;
      padding-bottom: 55px;
    }
    .slick-list{
      margin: 0 -15px;
      .slick-track{
        @include flexbox;
        .slick-slide{
          height: auto;
          margin:15px;
          min-height: 100%;
        }
      }
    }
    .slick-arrow{
      background: transparent;
      border: none;
      font-size: 0;
      outline: none;
      position: absolute;
      right: calc(50% - 60px);
      bottom: 0;
      &.slick-prev{
        right: calc(50% + 20px);
        transform: rotate(-180deg);
      }
      &::before{
        background: url(../img/new-images/right-double-arrow.png);
        content: "";
        display: block;
        height: 26px;
        width: 27px;
        background-repeat: no-repeat;
      }
    }
  }
}
.feature-section .feature-items{
  padding: 0;
  @include media(sm) {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.feature-section .feature-items .feature-item .thumb {
  width: 80px;
  height: 80px;
  padding: 12px;
  border: 7px solid #ebe4f2;
  margin:0;
  transition: 0.2s ease all;
}
.thumb-container:before,.thumb-container:after {
  content: "";
  width: 100px;
  height: 100px;
  background: #f1edf6;
  display: block;
  z-index: 1;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  @include border-radius(100%);
}
.feature-item:hover .thumb-container:before,.feature-item:hover .thumb-container:after{
opacity: 0;
}
.feature-item:hover .thumb-container .thumb{
border-color: transparent;
background-color:transparent;
}

.thumb-container:after {
  width: 120px;
  height: 120px;
  background: #f8f6fb;
  z-index: 0;
}
.thumb-container {
  position: relative;
  padding: 25px;
  display: inline-block;
}
.feature-section .feature-items .feature-item .thumb:after{
  background: url(../img/svg/fround.svg);
}

