/* Mobile Menu */
.mobile-nav {
  display: block;
  background: rgba(250, 250, 254, 0.95);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  overflow: auto;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  opacity: 0;
  visibility: hidden;
  /* transform: translateX(-100%); */
}
.mobile-nav.open {
  opacity: 1;
  visibility: visible;
  /* transform: translateX(0); */
}
.mobile-nav.open .mobile-nav-inner ul li {
  -webkit-transform: translateX(0%);
  transform: translateX(0%);
  opacity: 1;
}
.mobile-nav.open .mobile-nav-inner ul li:nth-child(1) {
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}
.mobile-nav.open .mobile-nav-inner ul li:nth-child(2) {
  -webkit-transition-delay: 0.15s;
  transition-delay: 0.15s;
}
.mobile-nav.open .mobile-nav-inner ul li:nth-child(3) {
  -webkit-transition-delay: 0.2s;
  transition-delay: 0.2s;
}
.mobile-nav.open .mobile-nav-inner ul li:nth-child(4) {
  -webkit-transition-delay: 0.25s;
  transition-delay: 0.25s;
}
.mobile-nav.open .mobile-nav-inner ul li:nth-child(5) {
  -webkit-transition-delay: 0.3s;
  transition-delay: 0.3s;
}
.mobile-nav.open .mobile-nav-inner ul li:nth-child(6) {
  -webkit-transition-delay: 0.35s;
  transition-delay: 0.35s;
}
.mobile-nav.open .mobile-nav__close {
  -webkit-transform: translateY(0%);
  transform: translateY(0%);
  opacity: 0.6;
  -webkit-transition-delay: 0.3s;
  transition-delay: 0.3s;
}
.mobile-nav .mobile-nav__close {
  width: 50px;
  height: 50px;
  position: absolute;
  right: 15px;
  top: 15px;
  text-align: center;
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
  opacity: 0;
  transition: opacity 0.5s, -webkit-transform 0.5s;
  transition: opacity 0.5s, transform 0.5s;
}
.mobile-nav .mobile-nav__close .svg-close {
  margin-top: 10px;
  width: 30px;
  height: 30px;
}
.mobile-nav .mobile-nav-inner {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.mobile-nav .mobile-nav-inner ul {
  margin: 0;
  padding: 0;
}
.mobile-nav .mobile-nav-inner ul li {
  margin: 0;
  padding: 5px 25px;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
  opacity: 0;
  transition: opacity 0.5s, -webkit-transform 0.5s;
  transition: opacity 0.5s, transform 0.5s;
}
.mobile-nav .mobile-nav-inner ul li.sub {
  position: relative;
}
.mobile-nav .mobile-nav-inner ul li.sub .darrow {
  display: block;
  width: 50px;
  height: 50px;
  position: absolute;
  right: 25px;
  top: 5px;
  cursor: pointer;
}
.mobile-nav .mobile-nav-inner ul li.sub .darrow:before, .mobile-nav .mobile-nav-inner ul li.sub .darrow:after {
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 2px;
  background: #000;
  top: 22px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.mobile-nav .mobile-nav-inner ul li.sub .darrow:before {
  left: 15px;
  transform: rotate(90deg);
}
.mobile-nav .mobile-nav-inner ul li.sub .darrow:after {
  left: 15px;
}
.mobile-nav .mobile-nav-inner ul li.sub .darrow.sub-close:before {
  transform: rotate(180deg);
}
.mobile-nav .mobile-nav-inner ul li.sub .dropdown {
  display: none;
  background: #222222;
  padding: 15px 20px;
  -webkit-border-radius: 10px;
  border-radius: 10px;
}
.mobile-nav .mobile-nav-inner ul li.sub .dropdown li {
  padding: 0;
}
.mobile-nav .mobile-nav-inner ul li.sub .dropdown li a {
  padding: 10px 0;
  font-size: 20px;
  display: block;
  color: #fff;
}
.mobile-nav .mobile-nav-inner ul a {
  padding: 10px 0px;
  display: inline-block;
  font-size: 24px;
  font-weight: 300;
  color: #000;
  position: relative;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.mobile-nav .mobile-nav-inner ul a:hover {
  color: #a757e8;
}
@media (min-width: 992px) {
  .mobile-nav {
    display: none;
  }
}

